<template>
  <view class="dashboard-container">
    <!-- 顶部统计卡片 -->
    <view class="stats-row">
      <view class="stats-card" v-for="(item, index) in statsCards" :key="index">
        <view class="stats-value">{{ item.value }}</view>
        <view class="stats-label">{{ item.label }}</view>
      </view>
    </view>

    <!-- 待办事项 -->
    <view class="todo-section">
      <view class="section-header">
        <text class="section-title">待办事项</text>
        <text class="more-link" @click="navigateTo('/pages_admin/order/index')"
          >查看全部</text
        >
      </view>
      <view class="todo-list">
        <view
          class="todo-item"
          v-for="(item, index) in todoList"
          :key="index"
          @click="handleTodo(item)"
        >
          <view class="todo-icon" :style="{ backgroundColor: item.color }">{{
            item.icon
          }}</view>
          <view class="todo-content">
            <view class="todo-title">{{ item.title }}</view>
            <view class="todo-desc">{{ item.desc }}</view>
          </view>
          <view class="todo-count" v-if="item.count > 0">{{ item.count }}</view>
        </view>
      </view>
    </view>

    <!-- 数据图表 -->
    <view class="chart-section">
      <view class="section-header">
        <text class="section-title">数据概览</text>
        <text
          class="more-link"
          @click="navigateTo('/pages_admin/analysis/index')"
          >详细分析</text
        >
      </view>
      <view class="chart-tabs">
        <view
          class="chart-tab"
          v-for="(tab, index) in chartTabs"
          :key="index"
          :class="{ active: currentTab === index }"
          @click="currentTab = index"
        >
          {{ tab }}
        </view>
      </view>
      <view class="chart-content">
        <view class="chart-placeholder">
          <!-- 实际项目中这里应该使用图表组件 -->
          <view class="chart-tips"
            >此处应显示{{ chartTabs[currentTab] }}图表</view
          >
        </view>
      </view>
    </view>

    <!-- 快捷功能 -->
    <view class="quick-actions">
      <view class="section-header">
        <text class="section-title">快捷功能</text>
      </view>
      <view class="actions-grid">
        <view
          class="action-item"
          v-for="(item, index) in quickActions"
          :key="index"
          @click="navigateTo(item.url)"
        >
          <view class="action-icon">{{ item.icon }}</view>
          <view class="action-text">{{ item.text }}</view>
        </view>
      </view>
    </view>

    <!-- 重要通知 -->
    <view class="notice-section">
      <view class="section-header">
        <text class="section-title">重要通知</text>
        <text class="more-link">全部通知</text>
      </view>
      <view class="notice-list">
        <view class="notice-item" v-for="(item, index) in notices" :key="index">
          <view class="notice-title">{{ item.title }}</view>
          <view class="notice-time">{{ item.time }}</view>
        </view>
        <view class="empty-tip" v-if="notices.length === 0">
          <text>暂无通知</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      statsCards: [
        { label: "今日订单", value: 12 },
        { label: "待处理", value: 5 },
        { label: "本月交易额", value: "¥3.2万" },
      ],
      todoList: [
        {
          title: "待审核订单",
          desc: "有新的POS机申请待审核",
          count: 3,
          icon: "📝",
          color: "#FF9800",
          type: "review",
        },
        {
          title: "待发货订单",
          desc: "有订单已审核通过待发货",
          count: 2,
          icon: "📦",
          color: "#2196F3",
          type: "ship",
        },
        {
          title: "待安装订单",
          desc: "有订单等待上门安装",
          count: 0,
          icon: "🔧",
          color: "#4CAF50",
          type: "install",
        },
        {
          title: "押金退还申请",
          desc: "有新的押金退还请求",
          count: 1,
          icon: "💰",
          color: "#9C27B0",
          type: "refund",
        },
      ],
      chartTabs: ["申请转化率", "产品分布", "区域分布"],
      currentTab: 0,
      quickActions: [
        { text: "添加产品", icon: "➕", url: "/pages_admin/product/add" },
        { text: "客户管理", icon: "👥", url: "/pages_admin/customer/index" },
        { text: "订单管理", icon: "📋", url: "/pages_admin/order/index" },
        { text: "押金管理", icon: "💳", url: "/pages_admin/deposit/index" },
        { text: "数据分析", icon: "📊", url: "/pages_admin/analysis/index" },
        { text: "系统设置", icon: "⚙️", url: "/pages_admin/settings/index" },
      ],
      notices: [
        {
          title: "系统将于本周五晚间22:00-24:00进行升级维护",
          time: "2023-10-25 10:30",
        },
        {
          title: "新的费率政策将于下月1日起实施，请查看详情",
          time: "2023-10-20 14:15",
        },
      ],
    };
  },
  onLoad() {
    // 加载管理面板数据
    this.loadDashboardData();
  },
  methods: {
    // 加载仪表盘数据
    loadDashboardData() {
      // 实际项目中应从服务器获取数据
      console.log("加载管理面板数据");
    },

    // 导航到指定页面
    navigateTo(url) {
      uni.navigateTo({
        url: url,
      });
    },

    // 处理待办事项
    handleTodo(item) {
      switch (item.type) {
        case "review":
          this.navigateTo("/pages_admin/order/index?status=pending_review");
          break;
        case "ship":
          this.navigateTo("/pages_admin/order/index?status=pending_shipping");
          break;
        case "install":
          this.navigateTo("/pages_admin/order/index?status=pending_install");
          break;
        case "refund":
          this.navigateTo("/pages_admin/deposit/index?status=pending_refund");
          break;
      }
    },
  },
};
</script>

<style>
.dashboard-container {
  padding: 30rpx;
  background-color: #f5f5f5;
  min-height: 100vh;
}

.stats-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30rpx;
}

.stats-card {
  flex: 1;
  background-color: #fff;
  border-radius: 12rpx;
  padding: 20rpx;
  margin: 0 10rpx;
  text-align: center;
  box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.05);
}

.stats-card:first-child {
  margin-left: 0;
}

.stats-card:last-child {
  margin-right: 0;
}

.stats-value {
  font-size: 36rpx;
  font-weight: bold;
  color: #1296db;
  margin-bottom: 10rpx;
}

.stats-label {
  font-size: 24rpx;
  color: #666;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20rpx;
}

.section-title {
  font-size: 32rpx;
  font-weight: bold;
  color: #333;
}

.more-link {
  font-size: 24rpx;
  color: #1296db;
}

.todo-section,
.chart-section,
.quick-actions,
.notice-section {
  background-color: #fff;
  border-radius: 12rpx;
  padding: 30rpx;
  margin-bottom: 30rpx;
  box-shadow: 0 2rpx 10rpx rgba(0, 0, 0, 0.05);
}

.todo-list {
  margin-top: 20rpx;
}

.todo-item {
  display: flex;
  align-items: center;
  padding: 20rpx 0;
  border-bottom: 1rpx solid #f5f5f5;
}

.todo-item:last-child {
  border-bottom: none;
}

.todo-icon {
  width: 80rpx;
  height: 80rpx;
  border-radius: 12rpx;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40rpx;
  margin-right: 20rpx;
  color: #fff;
}

.todo-content {
  flex: 1;
}

.todo-title {
  font-size: 28rpx;
  font-weight: bold;
  color: #333;
  margin-bottom: 6rpx;
}

.todo-desc {
  font-size: 24rpx;
  color: #999;
}

.todo-count {
  min-width: 40rpx;
  height: 40rpx;
  border-radius: 20rpx;
  background-color: #ff5252;
  color: #fff;
  font-size: 24rpx;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10rpx;
}

.chart-tabs {
  display: flex;
  border-bottom: 1rpx solid #f5f5f5;
  margin-bottom: 20rpx;
}

.chart-tab {
  padding: 15rpx 30rpx;
  font-size: 28rpx;
  color: #666;
  position: relative;
}

.chart-tab.active {
  color: #1296db;
}

.chart-tab.active:after {
  content: "";
  position: absolute;
  bottom: -1rpx;
  left: 30rpx;
  right: 30rpx;
  height: 4rpx;
  background-color: #1296db;
  border-radius: 2rpx;
}

.chart-content {
  height: 400rpx;
}

.chart-placeholder {
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  border-radius: 8rpx;
  display: flex;
  align-items: center;
  justify-content: center;
}

.chart-tips {
  font-size: 28rpx;
  color: #999;
}

.actions-grid {
  display: flex;
  flex-wrap: wrap;
}

.action-item {
  width: 33.33%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30rpx;
}

.action-icon {
  width: 100rpx;
  height: 100rpx;
  background-color: #f5f5f5;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50rpx;
  margin-bottom: 15rpx;
}

.action-text {
  font-size: 26rpx;
  color: #333;
}

.notice-list {
  margin-top: 20rpx;
}

.notice-item {
  padding: 20rpx 0;
  border-bottom: 1rpx solid #f5f5f5;
}

.notice-item:last-child {
  border-bottom: none;
}

.notice-title {
  font-size: 28rpx;
  color: #333;
  margin-bottom: 10rpx;
}

.notice-time {
  font-size: 24rpx;
  color: #999;
}

.empty-tip {
  padding: 30rpx 0;
  text-align: center;
  font-size: 28rpx;
  color: #999;
}
</style>
